import React from 'react';

// components
import Card from './Card.js'

// Images 
import Boozy from '../assets/images/logo.png'
import Search from '../assets/images/Search.png'
import { Container, Row } from 'react-bootstrap';

class Caro extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            items: [
                {
                    id: 0, 
                    title: 'Boozy App',
                    subTitle: 'Find happy hours near you', 
                    imgSrc: Boozy,
                    link: 'https://www.youtube.com/watch?v=Jc6_Rv82WOg',
                    selected: false
                },
                {
                    id: 1, 
                    title: 'Search Engine',
                    subTitle: 'Java App to parse webpages',
                    imgSrc: Search, 
                    link: 'https://github.com/nedyah/LooksyLoo',
                    selected: false
                }
            ]
        }
    }

    handleCardClick = (id, card) => {
        let items = [...this.state.items]

        items[id].selected = items[id].selected ? false : true;

        items.forEach(item => {
            if(item.id !== id) {
                item.selected = false;
            }
        });
        
        this.setState({
            items 
        })
    }

    makeItems = (items) => {
        return items.map(item => {
            return <Card item={item} click={e => this.handleCardClick(item.id, e)} key={item.id} />
        })
    }

    render() {
        return(
            <Container fluid={true}>
                <Row className="justify-content-around">
                    {this.makeItems(this.state.items)}

                </Row>
            </Container>
        )
    }
}

export default Caro;